<template>
  <v-sheet :color="color" class=" mb-3 mx-3 " width="100%" elevation="1" >
    <v-toolbar class="mb-3" dense flat :color="color">
      <v-toolbar-title class="title ">{{title}}</v-toolbar-title>
      
    </v-toolbar>
  

    <v-row class="mx-1">
      <v-col class="mx-2 pa-0 px-2" v-for="(item, ind) in departments" :key="ind" :cols="$vuetify.breakpoint.name == 'xs' ? 12 : null">
         
        <fieldset class="px-2 mb-2 fielset">
          <legend>
            {{ item._id.name }} 
          </legend>
          <v-row>
            <v-col ><small>Deposits</small><h2>{{ item.count || 0 }}</h2></v-col>
            <v-col> <small>Total</small><h2>{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount || 0)}}</h2></v-col>
          </v-row>
            <v-btn icon @click="goToDeposits(item._id._id)">
              <v-icon>mdi-table</v-icon>
            </v-btn>
          </fieldset>
      </v-col>
     
      <v-col class="mx-2 pa-0 px-2" :cols="$vuetify.breakpoint.name == 'xs' ? 12 : null" sm="">
        <fieldset class="px-2 mb-2 fielset">
          <legend>Total </legend>
          <v-row>
            <v-col ><small>Deposits</small><h2>{{ total.count || 0 }}</h2></v-col>
            <v-col> <small>Total</small><h2>{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(total.amount || 0)}}</h2></v-col>
          </v-row>
          <v-btn icon @click="goToDeposits(null)">
              <v-icon>mdi-table</v-icon>
            </v-btn>
        </fieldset>
      </v-col> 
    </v-row>
    
  </v-sheet>
</template>
<script>
 
import { mapActions } from 'vuex'
export default {
  name: 'TotalSheet',
  props: {
    title: String,
    id: String,
    dates: Array,
    color: {
      type: String,
      default: 'white'
    }
  },
  data: () => ({
    loading: false,
    departments: [],
    departmentsNames: [],
    total: {
      count: 0,
      amount: 0
    }
  }),
  watch: {
    dates:{
      handler: 'fetchTotal',
      immediate: true
    }
  },
  methods: {
    ...mapActions({
      get: 'api/get',
    }),
    goToDeposits (department) {
      const query = {
        business: this.id,
        created_on: this.dates.join('|')
      }
      if (department)
        query.department = department


      this.$router.push({
        path: '/deposit',
        query
      })  
      
    }, 
    async fetchTotal () {
      this.loading = true
      const departments = await this.get({ collection: 'department', query: {
        sortBy: ['name'], sortDesc: [false]
      }   })
      if (departments && departments.status == 200){
        this.departmentsNames = departments.data.docs
      }
      const departmentTotal = await this.get({ collection: 'dashboard/business', query:{
        'created_on':  this.dates.join('|'), 
        business: this.id 
      } })
      if (departmentTotal && departmentTotal.status == 200){
        this.departments = departmentTotal.data.departmentsTotal
      }
      const total = await this.get({ collection: 'dashboard/total', query:{
        'created_on':  this.dates.join('|'), 
        business: this.id 
      }
      })
      if (total && total.status == 200){
        this.total.count = total.data && total.data[0] ? total.data[0].count : 0
        this.total.amount = total.data && total.data[0] ? total.data[0].amount : 0
      }
      this.loading = false

    }
  } 
}
</script>
<style   scoped>
.fielset {
  border: 1px solid gray;
  border-radius: 5px;
}
</style>