<template>
  <v-content class="ma-0 pa-0">
    <v-row>
      <v-col sm="6"> <app-panel :loading="loading" title="Subtotal" currency="SAR"  :count="subtotal.count" :amount="subtotal.amount"></app-panel> </v-col>
      <v-col sm="6"> <app-panel :loading="loading" title="Withdraw" currency="SAR" :count="withdraw.count" :amount="withdraw.amount" color="red"></app-panel> </v-col>
      <v-col sm="6"> <app-panel :loading="loading" title="Total SAR" currency="SAR" :count="totalSAR.count" :amount="totalSAR.amount"></app-panel> </v-col>
      <v-col sm="6"> <app-panel :loading="loading" title="Total USD" :count="totalUSD.count" :amount="totalUSD.amount"></app-panel> </v-col>
    </v-row>



<v-sheet  class="my-2"   width="100%" elevation="1" v-for="wakel in wakels" :key="wakel._id" >
    <v-toolbar class="mb-3" dense flat  >
      <v-toolbar-title class="title ">{{wakel.displayName}}</v-toolbar-title>
    </v-toolbar>
 
    <v-row class="mx-1" >
      <v-col class="mx-2 pa-0 px-2" :cols="$vuetify.breakpoint.name == 'xs' ? 12 : null" sm="">
        <fieldset class="px-2 mb-2 fielset">
          <legend>Subtotal SAR</legend>
          <v-row>
            <v-col ><small>Deposits</small><h4>{{  wakel.total && wakel.total.subtotal ? wakel.total.subtotal.count : 0 }}</h4></v-col>
            <v-col> <small>Total</small><h4>{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'SAR',  }).format(wakel.total && wakel.total.subtotal ? wakel.total.subtotal.amount : 0)}}</h4></v-col>
          </v-row>
        </fieldset>
      </v-col> 
     <v-col class="mx-2 pa-0 px-2" :cols="$vuetify.breakpoint.name == 'xs' ? 12 : null" sm="">
        <fieldset class="px-2 mb-2 fielset">
          <legend class="red--text">Withdraw SAR</legend>
          <v-row>
            <v-col ><small class="red--text">Deposits</small><h4 class="red--text">{{ wakel.total && wakel.total.withdraw ? wakel.total.withdraw.count : 0 }}</h4></v-col>
            <v-col> <small class="red--text">Total</small><h4 class="red--text">{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'SAR',  }).format(wakel.total && wakel.total.withdraw ? wakel.total.withdraw.amount : 0)}}</h4></v-col>
          </v-row>
        </fieldset>
      </v-col> 
      <v-col class="mx-2 pa-0 px-2" :cols="$vuetify.breakpoint.name == 'xs' ? 12 : null" sm="">
        <fieldset class="px-2 mb-2 fielset">
          <legend>Total SAR</legend>
          <v-row>
            <v-col ><small>Deposits</small><h4>{{ wakel.total ? wakel.total.count : 0 }}</h4></v-col>
            <v-col> <small>Total</small><h4>{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'SAR',  }).format(wakel.total ? wakel.total.amount : 0)}}</h4></v-col>
          </v-row>
        </fieldset>
      </v-col>  
      <v-col class="mx-2 pa-0 px-2" :cols="$vuetify.breakpoint.name == 'xs' ? 12 : null" sm="">
        <fieldset class="px-2 mb-2 fielset">
          <legend>Total USD</legend>
          <v-row>
            <v-col ><small>Deposits</small><h4>{{wakel.total ? wakel.total.count : 0 }}</h4></v-col>
            <v-col> <small>Total</small><h4>{{ new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(wakel.total ? wakel.total.amount /3.9 : 0)}}</h4></v-col>
          </v-row>
        </fieldset>
      </v-col>  
    </v-row>  
    
  </v-sheet>

 
  </v-content>
</template>
<script>
import { mapActions } from 'vuex'
import AppPanel from './Panel'
export default {
  name: 'WakelsTotalDashboard',
  components: {
    AppPanel
  },
  props: {
    dates: Array
  },
  data () {
    return {
      loading: false,
      subtotal: { count: 0, amount: 0},
      withdraw: { count: 0, amount: 0},
      totalSAR: { count: 0, amount: 0},
      totalUSD: { count: 0, amount: 0},
      wakels: []
    }
  },
  watch: {
    dates: {
      handler: 'fetchTotal',
      immediate: true
    }
  },
  methods: {
    ...mapActions({
      get: 'api/get'
    }),
    async fetchTotal () { 
      // get totals
      this.loading = true
      const total = await this.get({ collection: 'transaction/total', query: {
        created_on: this.dates.join('|')
      } })
      if (total.status == 200 ) {
        const [ allTotal ] = total.data
        
        this.subtotal.amount = allTotal && allTotal.subtotal ? allTotal.subtotal.amount : 0
        this.subtotal.count = allTotal && allTotal.subtotal ? allTotal.subtotal.count : 0

        this.withdraw.amount = allTotal && allTotal.withdraw ? allTotal.withdraw.amount : 0
        this.withdraw.count = allTotal && allTotal.withdraw ? allTotal.withdraw.count : 0

        this.totalSAR.amount = allTotal && allTotal.amount ? allTotal.amount : 0
        this.totalSAR.count = allTotal && allTotal.count ? allTotal.count : 0

        this.totalUSD.amount = allTotal && allTotal.amount ? allTotal.amount / 3.9  : 0
        this.totalUSD.count = allTotal && allTotal.count ? allTotal.count   : 0
      
      }


      // get wakels 
      const users = await this.get({ collection: 'user', query: { role: 'wakel' } })
      this.wakels = await Promise.all(users.data.docs.map(async wakel => {
        
        const totalWakel = await this.get({ collection: 'transaction/total', query:{ 
          created_on: this.dates.join('|'),
          user: wakel._id
        } })
        wakel.total = totalWakel.data[0] 
        
        return wakel
      }))

   
     
       


      this.loading = false
    }
  }
}
</script>
<style   scoped>
.fielset {
  border: 1px solid gray;
  border-radius: 5px;
}
</style>