<template>
<v-row>
  <v-data-table 
  :headers="headers"
  :expanded="expanded"
  show-expand
  single-expand
 item-key="_id"
  :items="items"
  :loading="loading" 
  style="width:100%"
>
    <template v-slot:loading   >
        <v-skeleton-loader
        class="mt-6"
        height="94"
        type="list-item-three-line"
      >
      </v-skeleton-loader>
    </template>
    <template v-slot:item.amount="{ item }" >
        {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
    </template>   

    <template v-slot:expanded-item="{ headers, item }">
        <td :colspan="headers.length" class="pa-4">
          <v-data-table 
          :headers="dataHeaders" 
          :items="item.child" 
          @click:row="goTo"
          >
            <template v-slot:item.amount="{ item }" >
                {{  new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD',  }).format(item.amount) }}  
            </template>   
            
            <template v-slot:item.created_on="{ item }"  >
              {{ dateformat(new Date(item.created_on), 'yyyy-mm-dd') }}
            </template>
            
            
          </v-data-table>
        </td>
      </template> 
     
  </v-data-table>
</v-row>
</template>

<script>
import dateformat from 'dateformat'
// import utility from './../../utility'
 
import { mapActions } from 'vuex'
export default {
  name: 'AgentReport',
   props: {
    dates: { type: Array}
  },
  data () {
    return {
      dateformat,
      headers: [{
            text: 'Owner',
            align: 'start',
            value: 'user.displayName',
        },
        { text: 'Deposits', value: 'count' },
        { text: 'Total', value: 'amount' }],
      
      
      dataHeaders: [{
            text: 'Owner',
            align: 'start',
            value: 'customer.tpc',
        },
        {
          text: 'Account name',
          align: 'start',
          value: 'customer.name',
        },
        
        { text: 'Deposits', value: 'count' },
        { text: 'Total', value: 'amount' }],
      items: [],
      loading: false,

      expanded: []
    }
  },
  computed:{
    user () {
      return this.$store.state.auth.user
    }
  },
  methods:{
    ...mapActions({
      get: 'api/get'
    }),
    async update () {
      this.loading = true
      const result = await this.get({
          collection: 'report_new',
          query: {
            department: this.user.department || null,
            by: 'user',
            created_on: this.dates.join('|'),
          }
        })

    
      if (result.data) {
        this.items = result.data 
      } 
      
      this.loading = false
    },
    goTo (item) {
      console.log(item);
    }
  },
  watch: {
    dates: {
      handler: 'update',
      immediate: true
    } 
  } 
}
</script>