<template>
  <v-card flat color="transparent" >
    <v-card-title>
      {{ dateString  }}
      <v-spacer></v-spacer>
    </v-card-title>
    <v-card-text>
      <v-btn-toggle v-model="tab" >
        <v-btn  @click="today">
          <v-icon>mdi-calendar-today</v-icon>
          Today
        </v-btn>
        <!-- <v-btn  @click="week" v-if="$vuetify.breakpoint.smAndUp">
          <v-icon>mdi-calendar-week</v-icon>
          Last 7 days
        </v-btn> -->
        <v-btn @click="month">
          <v-icon>mdi-calendar-month</v-icon>
          This month
        </v-btn>
        <v-btn @click="dialog = true" v-if="$vuetify.breakpoint.mdAndUp">
          <v-icon>mdi-calendar-range</v-icon>
          Select a date range
        </v-btn>
      </v-btn-toggle>
    </v-card-text>
    <v-dialog v-model="dialog" max-width="650">
      <v-card >
        <v-date-picker v-model="dates" @input="range" range full-width></v-date-picker>
      </v-card>
    </v-dialog>
  </v-card>
</template>
<script>
 
import utility from './../../utility'
export default {
  name: 'DatePicker',
  props: {
    value: { type: Array, default: () => utility.currentDateRange}
  },
 
  data() {
 
    return {
       dates: utility.currentDateRange,
       dialog: false,
       tab: 1
    }
   
  },
 
  computed: {
    dateString() {
      if (this.value[0] == this.value[1])
        return this.value[0]
      else 
        return this.value.join(' ~ ')
    }
  },
  methods: {
    range () {
      if (this.dates.length > 1) {
        this.dialog = false
        this.$emit('input', this.dates)
      }
    },
    today () {
       this.$emit('input', utility.todayString)
    },
    week () {
       this.$emit('input', utility.last7daysString)
    },
    month () {
       this.$emit('input', utility.currentDateRange)

    }
  },
  created () {
    console.log(this.$vuetify.breakpoint);
  }
  
  
}
</script>
 